<template>
	<div id="shop-coupon" :class="`shop-${shop_style}`">
		<public-header title="代金券"></public-header>
		<div class="content font-14" >
			<template v-if="(couponsCollect && couponsCollect.coupons && couponsCollect.coupons.length > 0) || (couponsKabao && couponsKabao.length > 0)">
				<template v-if="couponsCollect && couponsCollect.coupons && couponsCollect.coupons.length > 0">
					<div class="coupon-item" v-for="(item, index) in couponsCollect.coupons" :key="item.id">
						<div class="left">
							<div>
								<span class="font-14">{{Lang.dollarSign}}</span><span class="discount">{{item.condition}}</span>
							</div>
							<div class="margin-5-t ">满{{item.condition}}元使用</div>
						</div>
						<div class="right">
							<div class="w-100 ellipsis font-14 font-bold"><i class="icon icon-crownfill"></i>{{item.title}}</div>
							<div class="w-100 ellipsis c-gray margin-10-tb">全场通用</div>
							<div class="w-100 ellipsis c-gray">领取后{{item.use_days_limit}}天内有效</div>
							<template v-if="!item.isget">
								<div class="btn-collect" @click="onGetCoupon(couponsCollect.sid, index)">立即领取</div>
							</template>
							<template v-else>
								<div class="had">
									<img src="http://cos.lalawaimai.com/we7_wmall/wxapp/gone.png" class="img-100">
								</div>
								<router-link class="btn-collect" :to="util.getUrl({path: '/shop/pages/home/index', query:{sid: couponsCollect.sid}})">
									立即使用
								</router-link>
							</template>
						</div>
					</div>
				</template>
				<template v-if="couponsKabao && couponsKabao.length > 0">
					<div class="flex-lr font-12 margin-15 ">
						<div>以下优惠券限门店会员领取</div>
						<div class="kabao-bind" @click="util.jsUrl( '/plugin/pages/kabao/detail', {sid: sid}, 'push')">开通会员</div>
					</div>
					<div class="coupon-item kabao" v-for="(item, index) in couponsKabao" :key="item.id">
						<div class="left">
							<div>
								<span class="font-14">{{Lang.dollarSign}}</span><span class="discount">{{item.discount}}</span>
							</div>
							<div class="margin-5-t ">满{{item.condition}}元使用</div>
						</div>
						<div class="right">
							<div class="w-100 ellipsis font-14 font-bold"><i class="icon icon-crownfill"></i>门店会员优惠券</div>
							<div class="w-100 ellipsis c-gray margin-10-tb">全场通用</div>
							<div class="w-100 ellipsis c-gray">领取后{{item.use_days_limit}}天内有效</div>
							<div class="btn-collect" @click="onGetKabao()">立即领取</div>
						</div>
					</div>
				</template>
			</template>
			<iloading v-if="showPreLoading"></iloading>
		</div>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import {getData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				sid: 0,
				showPreLoading: true,
				shop_style: '',
				couponsCollect: [],
				couponsKabao: [],
			}
		},
		components: {
			PublicHeader,
		},
		methods: {
			onLoad(force) {
				this.util.request({
					url: 'wmall/shop/coupon/collect',
					data: {
						sid: this.sid,
					},
				}).then((res) => {
					this.showPreLoading = false;
					let result = res.data.message;
					if(result.errno) {
						this.$toast(result.message);
						return;
					}
					result = result.message;
					this.couponsCollect = result.couponsCollect;
					if(result.couponsKabao.errno) {
						result.couponsKabao = [];
					}
					this.couponsKabao = result.couponsKabao;
				});

			},

			onGetKabao() {
				let that = this;
				that.$dialog.confirm({
					title: '开通会员才能领取',
					message: '请问要现在去开通吗?'
				}).then(() => {
					this.$router.push(that.util.getUrl({path: '/plugin/pages/kabao/detail', query: {sid: that.sid}}));
					return;
				})
			},

			onGetCoupon(sid, index) {
				this.util.request({
					url: 'wmall/channel/coupon/get',
					data: {
						sid: sid
					},
				}).then((res) => {
					let result = res.data.message;
					if(!result.errno) {
						this.couponsCollect.coupons[index].isget = true;
					}
					this.$toast(result.message);
				})
			}
		},

		created() {
			var query = this.util.parseQuery(this.$route.query);
			this.sid = query.sid;
		},
		mounted(){
			this.onLoad();
		}
	}
</script>

<style>
	@import "../../../static/css/shop.css";
	#shop-coupon .coupon-item{
		margin: 10px 15px;
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: space-between;
		background-color: #EEF9F1;
		font-size: 12px;
		min-height: 10px;
		border-radius: 5px;
		border: 1px solid #D6E2D8;
		overflow: hidden;
	}
	#shop-coupon .coupon-item.kabao{
		background-color: #FFEA9B;
		border-color: #E1D8BA;
	}
	#shop-coupon .coupon-item .left{
		width: 30%;
		height: 100%;
		display: flex;
		flex-flow: column nowrap;
		align-items: center;
		justify-content: center;
		color: #3FB765;
	}
	#shop-coupon .coupon-item.kabao .left{
		color: #333333;
	}
	#shop-coupon .coupon-item .right{
		position: relative;
		width: 70%;
		display: flex;
		flex-flow: column nowrap;
		align-items: flex-start;
		justify-content: space-around;
		padding: 15px 10px;
		background-color: #fff;
		border-left: 1px solid #D6E2D8;
	}
	#shop-coupon .coupon-item .discount{
		font-size: 24px;
		font-weight: bold;
	}
	#shop-coupon .coupon-item .btn-collect{
		position: absolute;
		z-index: 2;
		right: 10px;
		top: 50%;
		transform: translateY(-50%);
		background-color: #36C85A;
		color: #fff;
		border-radius: 50px;
		padding: 7px 10px;
	}
	#shop-coupon .coupon-item.kabao .btn-collect{
		background-color: #EBD170;
		color: #333;
	}
	#shop-coupon .kabao-bind{
		color: #3FB765;
	}
	#shop-coupon .coupon-item .icon-crownfill{
		display: none;
	}
	#shop-coupon .coupon-item.kabao .icon-crownfill{
		display: inline;
		margin-right: 5px;
		color: #EBD170;
	}
	#shop-coupon .coupon-item .had{
		position: absolute;
		top: 50%;
		left: 50%;
		z-index: 1;
		width: 70px;
		height: 50px;
		transform: translate(-50%, -50%);
	}


</style>


